import * as React from 'react';
import { useState } from 'react';
import Select from "react-select";
import { useForm , Controller} from "react-hook-form";
import { Checkbox, FormControlLabel, RadioGroup, Radio } from "@material-ui/core";
import axios from "axios";




function Contact(props) {

  const styleValue = ['html_css','jQuery','ECMA6','react','node','php'];
  const radioValue = ['','',' 8'];

  const [loading, setLoading] = useState(false);

  const { register, handleSubmit, control, formState: { errors } } = useForm({
    defaultValues: {
      company: '',
      email: '',
      gender: '',
      age : '',
      ability: [],      
      worktime: radioValue[0],
      select :{ value: "chocolate", label: "Chocolate" }
    }
  })
//sql  (  )    .
//          
//   INSERT INTO `bby_contact` (`id` , `company`, `email`, `gender`, `age`, `ability`, `worktime`, `select` )
//   VALUE (null, '','4thdraw@naver.com','f','50', '["html_css","php"]', '', '{"value":"strawberry","label":"Strawberry"}')
  
  const onSubmit = async (data) => {
    setLoading(true);
    alert(JSON.stringify(data));

    // try {
    //     const response = await axios.post("", {
    //       email,
    //       name,
    //       userpwd,
    //       phone,
    //     });
    //     console.log(response);
    //   } catch (error) {
    //     alert(error.response.data);
    // }

    setLoading(false);
  };



  return (
    <section className='container'>
        <div className="row">
            <div className='col-4'>
                 ????<br />
                 ?????<br />
                     ...
            </div>
            <div className='col'>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <ul>
                        <li>
                            <span></span>
                            <input {...register("company" ,  {
                                    required: true,
                                    
                                    }) } />
                            {errors.company && <p role="alert"> </p>}
                        </li>
                        <li>
                            <span></span><input {...register("email" ,  {
                            required: ' ',
                            pattern: {
                            value: /\S+@\S+\.\S+/,
                            message: "   .",
                            },
                        }) }  placeholder="test@email.com" />
                        {errors.email && <p role="alert">{errors.email.message}</p>}
                        </li>                            
                        <li>
                            <span></span>
                            <select {...register("gender", {required: true, message: " " })}>
                                <option value="f">female</option>
                                <option value="m">male</option>
                                <option value="o">other</option>
                            </select>
                            {errors.gender && <p role="alert">{errors.gender.message}</p>}
                        </li>
                        <li>
                           <span></span> 
                           <input type="number" {...register("age", { min: 18, max: 99 })} />
                           {errors.age && (
          <p>You Must be older then 18 and younger then 99 years old</p>
        )}
                        </li>
                        <li>
                        <Controller
                            name="select"
                            control={control}
                            render={({ field }) => <Select 
                            {...field} 
                            options={[
                                { value: "chocolate", label: "Chocolate" },
                                { value: "strawberry", label: "Strawberry" },
                                { value: "vanilla", label: "Vanilla" }
                            ]} 
                            />}
                        />
                        </li>
                        <li>
                            {
                                styleValue.map(( item, idx ) =>{
                                    return(
                                        <FormControlLabel
                                            control={
                                                <Checkbox                                                    
                                                    {...register('ability') }
                                                    value={item}
                                                />
                                            }
                                            label={item}
                                            />
                                    )

                                })
                            }
                        
                        </li>
                        <li>
                        <RadioGroup                          
                          className='flex-md-row '
                        >
                           {
                                radioValue.map(( item, idx ) =>{
                                    return(                                        
                                     <FormControlLabel value={item} {...register('worktime') }  control={<Radio />} label={item} />
                                    )

                                })
                            }
                              </RadioGroup>
                        </li>
                    </ul>                    
                    <input type="submit" />
                </form>
            </div>
        </div>
    </section>    
  );
  
}

export default Contact;